
<style scoped>

</style>


<template>
    <div>
        <el-card v-show="!show_add_board">
            
            <el-row>
                <el-col :span="8">
                    <el-input placeholder='请输入搜索内容' v-model="id_for_seek">
                        <el-button slot="append" icon="el-icon-search" 
                            @click="seekbyid"
                        ></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type='primary' @click="get_cats_lists">添加商品</el-button>
                </el-col>
            </el-row>
            <el-table
                v-if="goodlist"
                :data='goodlist'
            >
                <el-table-column 
                    prop='goods_id' label='#'
                />
                <el-table-column 
                    prop='goods_name' label='商品名称'
                />
                <el-table-column 
                    prop='goods_price' label='商品价格(元)'
                />
                <el-table-column
                    prop='goods_weight' label='商品重量'
                />
                <el-table-column
                    prop='add_time' label='创建时间'
                >
                    <template slot-scope="key_obj">
                        <div >{{ key_obj.row.add_time | changeformat }}</div>
                        
                    </template>
                </el-table-column>
                <el-table-column prop='option' label="操作" >
                    <div slot-scope="scoped">
                        <el-button type='primary' icon="el-icon-edit" @click="run_editor(scoped.row,'edit')"></el-button>
                        <el-button type='danger' icon='el-icon-delete' @click="run_editor(scoped.row,'del')"></el-button>
                    </div>
                </el-table-column>
            </el-table>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="current_page"
                :page-sizes="page_sizes"
                :page-size="page_size"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
        <!-- 添加-->
        <el-card 
            v-show="show_add_board"
            
        >
            <el-alert
                title='添加商品信息'
                center
                show-icon
                :closable=false
            />
            <!-- 步骤条 -->
            <el-steps 
                align-center
                :active='(a.step_index-0)'
            >
                <el-step 
                    v-for='(m,i) in a.steps' :key='i'
                    :title="m.step_name"
                />
                <el-step title='完成' />
            </el-steps>
            <el-form>
                <el-tabs 
                    tabPosition='left'
                    v-model="a.step_index"
                    :before-leave='before_leave_this_tab'
                >
                    <el-tab-pane 
                        v-for='(m,i) in a.steps' :key='i'
                        :label="m.step_name"
                        :name='m.index'

                    >
                        <div v-if="m.index=='0'">
                            <el-form-item label='商品名称'>
                                <el-input
                                    v-model="a.name"
                                ></el-input>
                            </el-form-item>
                            <el-form-item  label='商品价格'>
                                <el-input
                                    v-model="a.price"
                                ></el-input>
                            </el-form-item>
                            <el-form-item  label='商品重量'>
                                <el-input
                                    v-model="a.weight"
                                ></el-input>
                            </el-form-item>
                            <el-form-item  label='商品数量'>
                                <el-input
                                    v-model="a.number"
                                ></el-input>
                            </el-form-item>
                            <el-form-item  label='商品分类'
                                v-if="catlist"
                            >
                                <el-cascader
                                    v-model='a.cat'
                                    :options='catlist'
                                    :props="{ 
                                        expandTrigger: 'hover',
                                        label:'cat_name'
                                    }"
                                >

                                </el-cascader>
                            </el-form-item>
                        </div>
                        <div v-if="m.index=='1'">
                            <el-form-item :label='m.step_name'>
                                <!-- <el-checkbox-group>
                                    <el-checkbox></el-checkbox>
                                </el-checkbox-group> -->
                            </el-form-item>
                        </div>
                        <div v-if="m.index=='2'">
                            <el-form-item :label='m.step_name'>

                            </el-form-item>
                        </div>
                        <div v-if="m.index=='3'">
                            <el-form-item :label='m.step_name'>
                                
                            </el-form-item>
                        </div>
                        <div v-if="m.index=='4'">
                            <el-form-item :label='m.step_name'>
                                
                            </el-form-item>
                            <div >
                                <el-button @click="show_add_board=false">取消</el-button>
                                <el-button type='primary'>确定</el-button>
                            </div>
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
        <!-- 修改 面板 -->
        <el-dialog title="修改商品信息"
            :visible='show_edit_board'
        >
            <el-form v-if="r">
                <el-form-item label='商品名称'>
                    <el-input
                        v-model="r.goods_name"
                    ></el-input>
                </el-form-item>
                <el-form-item  label='商品价格'>
                    <el-input
                        v-model="r.goods_price"
                    ></el-input>
                </el-form-item>
                <el-form-item  label='商品重量'>
                    <el-input
                        v-model="r.goods_weight"
                    ></el-input>
                </el-form-item>
                <el-form-item  label='商品数量'>
                    <el-input
                        v-model="r.goods_number"
                    ></el-input>
                </el-form-item>
                <el-form-item  label='商品分类'
                >
                    <el-cascader
                        v-model='a.cat'
                        :options='catlist'
                        :props="{ 
                            expandTrigger: 'hover',
                            label:'cat_name'
                        }"
                        @change="cascader_change"
                    >

                    </el-cascader>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button @click="show_edit_board=false">取消</el-button>
                <el-button type='primary'>确定</el-button>
            </div>
        </el-dialog>
        <!-- 删除用户面板 -->
        <el-dialog title="删除商品"
            :visible='show_del_board'
        >
            <div slot="footer">
                <el-button @click="show_del_board=false">取消</el-button>
                <el-button type='primary' @click="del_sure(r)" >确定</el-button>
            </div>
        </el-dialog>
        
    </div>
</template>


<script>
export default {
    name:'productlist',
    data() {
        return {
            // 查找关键字
            id_for_seek:'',
            // 列表信息
            goodlist:null,
            // 商品分类列表
            catlist:null,
            // 分页信息
            current_page:1,
            page_sizes:[5,10,25,50,100],
            page_size:5,
            total:3,
            // 记录行信息
            r:null,
            // 添加面板开关
            show_add_board:false,
            // 编辑面板 开关
            show_edit_board:false,
            // 删除面板 开关
            show_del_board:false,
            // 添加面板数据
            a:{
                steps:[
                    {index:'0',step_name:'基本信息'},
                    {index:'1',step_name:'商品参数'},
                    {index:'2',step_name:'商品属性'},
                    {index:'3',step_name:'商品图片'},
                    {index:'4',step_name:'商品内容'}
                ],
                step_index:0,
                name:'',
                cat:[],
                price:'',
                number:'',
                weight:'',

                intr:'',
                pics:'',
                attrs:'',
            },
        };
    },
    created(){

    },
    mounted(){
        this.oninit()
    },
    //props:{},//[]
    methods:{
        // 查找
        async seekbyid(){
            if(!this.id_for_seek.trim()){
                this.$message('搜索框为空')
                return false
            }else{
                // 全等搜索
                let {data:res}=await this.$http.get(
                    `/goods`,{
                        params:{
                            query:this.id_for_seek,
                            pagenum:this.current_page,
                            pagesize:this.page_size,
                        }
                    }
                )
                console.log(res)
                if(res.data){
                    this.goodlist=res.data.goods
                    console.log('获取列表信息',this.goodlist)
                    this.total=res.data.total
                }else{
                    this.$message(res.meta.msg)
                }
            }
        },
        // 级联选择器 选择修改
        cascader_change(){

        },
        // tabs 禁止首页空置
        before_leave_this_tab(a,oa){
            console.log(a,oa)
            if(a>=1&&!this.step_on_off_one){
                this.$set(this.a,'step_index','0')
                this.$message('信息不能为空')
                return false;
            }
        },
        // 当单页条数发生变化
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.page_size=val
            this.oninit()
        },
        // 当页码发生变化
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.current_page=val
            this.oninit()
        },
        // 获取分类列表
        async get_cats_lists(){
            let {data:res}=await this.$http.get(
                `categories?type=3`,{params:{

                }}
            )
            this.catlist=res.data
            console.log('商品分类信息',this.catlist)
            this.show_add_board=true
        },
        async oninit(){
            let {data:res}=await this.$http.get(
                `/goods`,{params:{
                    pagenum:this.current_page,
                    pagesize:this.page_size,
                    query:''
                }}
            )
            console.log('页面初始化数据',res)
            this.goodlist=res.data.goods
            console.log('获取列表信息',this.goodlist)
            this.total=res.data.total
        },
        run_editor(r,k){
            this.r=r
            switch (k) {
                case 'edit':
                    this.show_edit_board=true
                break;
                case 'del':
                    this.show_del_board=true
                break;
            }
        },
        async add_sure(){

            let {data:res}=await this.$http.post(
                `/goods`,{
                    goods_name:'',
                    goods_cat:'',
                    goods_price:'',
                    goods_number:'',
                    goods_weight:'',

                    goods_introduce:'',
                    pics:'',
                    attrs:'',
                }
            )
            
            this.show_add_board=false
            this.oninit()
        },
        async edit_sure(r){
            let {data:res}=await this.$http.put(
                `/goods/${r.goods_id}`,{
                    goods_name:'',
                    goods_cat:'',
                    goods_price:'',
                    goods_number:'',
                    goods_weight:'',

                    goods_introduce:'',
                    pics:'',
                    attrs:'',
                }
            )
            console.log('编辑确定回执',res)
        },
        async del_sure(r){
            let {data:res}=await this.$http.delete(
                `/goods/${r.goods_id}`,{}
            )
            console.log(res)
            this.show_del_board=false
            this.oninit()

        }
    },
    computed:{
        step_on_off_one(){
            let ta=this.a
            let boo=ta.name&&ta.price&&ta.weight&&ta.number&&ta.cat[0]
            return boo
        },
    },
    watch:{

    },
    filters:{
        
        changeformat(v){
            let t=new Date(v+'000'-0)

            return t.toLocaleDateString()+' '+t.toLocaleTimeString()
        }
    },
    directives:{

    },
    components:{

    },
};

</script>

